import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import {
  ShowModalGroup,
  showModal,
  hideModal,
  GetDataGroup,
} from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import {
  NotifSucces,
  ToastNotification,
} from "../../../components/helpers/notification.jsx";
import { deleteData, postData, putData } from "../../../config/axios.jsx";
import { reset } from "redux-form";
import { ErrorHandling } from "../../../components/helpers/function.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import FormDataGroup from "./FormDataGroup.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";

const maptostate = (state) => {
  return {
    DataGroup: state.datamaster.GetDataGroup,
    hideModal: state.datamaster.modalDialog,
  };
};
const hapusDataGroup = (kode,params, dispatch) => {
  Swal.fire({
    // title: "Anda Yakin !!",
    // text: "Ingin Menghapus Data Ini ?",
    html:
      "Apakah Anda Yakin Ingin " +
      "Menghapus " +
      "<h1><b>Kode Group " +
      kode +
      "</b> ?</h1>",
    icon: "warning",
    position: "top-center",
    cancelButtonText: "Tidak",
    showCancelButton: true,
    confirmButtonText: "OK",
    showConfirmButton: true,
  }).then((result) => {
    if (result.isConfirmed) {
      deleteData("group/delete/by-kode-group/" + params).then(() => {
        NotifSucces("Data Berhasil Di Hapus").then(() =>
          dispatch(GetDataGroup())
        );
      }).catch((err) => {
        ToastNotification(
          "error", err.response.data 
        )
      })
    }
  });
};
class MasterGroup extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      isLoading: false,
      modalDialog: false,
      columns: [
        {
          dataField: "kode_group",
          text: "Kode Group",
          sort: true,
        },
        {
          dataField: "nama_group",
          text: "Nama Group",
        },
        {
          dataField: "jenis_group",
          text: "Jenis Group",
        },
        {
          dataField: "harga",
          text: "Harga",
          headerClasses : "text-right",
          formatter: (cell) => {
            return  <div className="text-right"> {cell===undefined ? 0 : cell.toLocaleString("kr-KO")} </div>
          },
        },
        {
          dataField: "harga_modal",
          text: "Harga Modal",
          headerClasses : "text-right",
          formatter: (cell) => {
            return  <div className="text-right"> {cell===undefined ? 0 : cell.toLocaleString("kr-KO")} </div>
          },
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              id: row._id,
              kode_group: row.kode_group,
              nama_group: row.nama_group,
              jenis_group: row.jenis_group,
              persentase: row.persentase.toFixed(1),
              harga: row.harga,
              harga_modal: row.harga_modal,
              is_berlian: row.is_berlian,
            };
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEditDataGroup(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() =>
                      hapusDataGroup(row.kode_group,row._id, this.props.dispatch)
                    }
                    className="btn btn-danger"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.props.dispatch(GetDataGroup());
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    let data_edit = {
      nama_group: data.nama_group,
      jenis_group: data.jenis_group,
      harga_modal: parseInt(data.harga_modal) || 0,
      kadar : 0,
      persentase: 0,
      is_berlian: data.is_berlian === "YA" ? true : false,
      harga: parseInt(data.harga || 0),
    };
    let data_post = {
      kode_group: data.kode_group,
      nama_group: data.nama_group,
      jenis_group: data.jenis_group,
      kadar : 0,
      is_berlian: data.is_berlian === "YA" ? true : false,
      harga_modal: parseInt(data.harga_modal) || 0,
      harga: parseInt(data.harga || 0),
      persentase: 0,
    };

    this.state.isEdit
      ? putData("group/edit/by-kode-group/" + data.id, data_edit)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Diupdate");
          })
          .then(() => {
            this.props.dispatch(GetDataGroup());
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .then(() => {
            this.props.dispatch(reset("ModalFormDataGroup"));
          })
        .catch((error) => {
          ToastNotification("info", error.response === undefined ? "Terjadi Kesalahan Saat Megirim Data " : error.response.data)
          this.setState({ isLoading: false });
        }
        )
      : postData("group/add", data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Disimpan");
          })
          .then(() => {
            this.props.dispatch(GetDataGroup());
          })
          .then(() => {
            this.props.dispatch(reset("ModalFormDataGroup"));
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .catch((error) =>
            ErrorHandling(
              error,
              "group/reactive/by-kode-group",
              // data.kode_group,
              data_post,
              this.props.dispatch,
              GetDataGroup(),
              hideModal(),
              "ModalFormDataGroup"
            ).then(() => {
              this.setState({ isLoading: false });
            })
          );
  }

  formEditDataGroup(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalGroup(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  ShowModalGroup() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalGroup(false));
    this.setState({
      isEdit: false,
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Master Group</li>
        </ol>
        <h1 className="page-header">Master Group </h1>
        <Panel>
          <PanelHeader>Master Group</PanelHeader>
          <br />

          {/* Master Group */}
          <div className="container">
            <Tabel
              keyField="kode_group"
              tambahData={true}
              handleClick={() => this.ShowModalGroup()}
              exportCsv={true}
              data={this.props.DataGroup}
              columns={this.state.columns}
              empty={this.props.DataGroup}
              textEmpty="Data Group Kosong"
            />
          </div>
          <br />
          {/* End Master Pinjaman */}
          <ModalGlobal
            size="P"
            title={
              this.state.isEdit
                ? "Edit Data Data Group"
                : "Tambah Data Data Group"
            }
            content={
              <FormDataGroup
                isLoading={this.state.isLoading}
                isEdit={this.state.isEdit}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />

          {/* End Tambah Master Group  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(MasterGroup);
